html,
body,
#root {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	overflow: hidden;
	// background-color: white;
}

.App {
	height: 100%;
}

.container {
	width: 100%;
	height: 100%;
	bottom: 0;
	display: flex;
	// align-items: center;
	justify-content: center;
	position: absolute;
	align-items: flex-end;
}

$lightBlend: #fcfae4;

.card1 {
	// min-width: 60ch;
	// min-height: 60ch;
	top: 20%;
	align-self: center;
	position: absolute;
	border: 8px solid rgb(240, 235, 197);
	will-change: transform;
	width: 17vw;
	height: 60vh;
	background: radial-gradient(#fff 30%, $lightBlend 100%);
	animation: callOfLions 10s infinite linear;

	&::after {
		content: '';
		position: absolute;
		width: 173%;
		height: 100%;
		top: 0;
		left: -35%;
		background: transparent url('../../assets/portal2.png') no-repeat;
		background-size: 100%;
		background-position: center;
		animation: rotating 5s linear infinite;
	}
}

@keyframes rotating {
	0%{
		filter: brightness(1);
		transform: rotate(0deg);
	}

	50% {
		filter: brightness(1.2);
		transform: rotate(180deg);
	}

	100% {
		filter: brightness(1);
		transform: rotate(360deg);
	}
}

.card2 {
	will-change: transform;
	background-image: url('../../assets/layered-lions.png');
	background-repeat: no-repeat;
	background-position: center bottom;
	background-size: contain;
	height: 60%;
	bottom: -10%;
	width: 100%;
}

@keyframes callOfLions {
	0% {
		background: radial-gradient(#fff 0%, $lightBlend 100%);
		box-shadow: 0 0 2px #fff,
			0 0 2px #fff,
			0 0 3px yellow,
			0 0 4px $lightBlend,
			0 0 5px $lightBlend,
	}

	50% {
		background: radial-gradient(#fff 30%, $lightBlend 100%);
		box-shadow: 0 0 20px #fff,
			0 0 30px #fff,
			0 0 40px #fff,
			0 0 50px #fff,
			0 0 60px #fff,

	}

	100% {
		background: radial-gradient(#fff 0%, $lightBlend 100%);
		box-shadow: 0 0 2px #fff,
			0 0 2px #fff,
			0 0 3px yellow,
			0 0 4px $lightBlend,
			0 0 5px $lightBlend,
	}
}

