.scene {
	height: 100%;
	width: 100vw;
	// background: green;
	position: relative;
	overflow: hidden; 
	background: radial-gradient(ellipse at bottom, #ea526a 0%, #2d1d46 70%, #090a0f 100%);

	&::after {
		content: '';
		position: absolute;
		background: radial-gradient(transparent 0%, #000 250%);
		height: 100%;
		width: 100%;
		pointer-events: none;
		top: 0;
	}

	canvas{
		
		
		background-image: url('../../assets/nebula-opt-2.jpg');
		// background: radial-gradient(ellipse at bottom, #ea526a 0%, #2d1d46 70%, #090a0f 100%);
		background-repeat: no-repeat;
		background-size: cover;
		// filter: contrast(1.6);
	}

	.scene-background {

	}

	.scene-landscape {
		// background: red;
		background-image: url('../../assets/layered-landscape.png');
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
		height: 100%;
		bottom: 0;
		width: 100%;
		position: absolute;
		filter: blur(1px);
	}

	.scene-lions {
		// background: blue;
		background-image: url('../../assets/layered-lions.png');
		background-repeat: no-repeat;
		background-position: center bottom;
		background-size: contain;
		height: 70%;
		bottom: 0;
		width: 100%;
		position: absolute;
	}
}

// n is number of stars required
@function multiple-box-shadow($n) {
	$value: "#{random(2000)}px #{random(2000)}px #FFF";

	@for $i from 2 through $n {
		$value: "#{$value} , #{random(2000)}px #{random(2000)}px #FFF";
	}

	@return unquote($value);
}

$shadows-small: multiple-box-shadow(700);
$shadows-medium: multiple-box-shadow(200);
$shadows-big: multiple-box-shadow(100);

